.main-nav {
  margin-left: auto;

  &__list {
    @include list-reset;
    display: flex;
  }

  &__item {
    padding-right: 22px;
    margin-right: 22px;
    border-right: 1px solid #E0E0E0;

    &:last-child {
      padding-right: 0;
      margin-right: 0;
      border-right: none;
    }
  }

  &__link {
    font-size: 14px;
    font-weight: 600;
    line-height: 20px;
    color: $color-night-rider;

    &.is-active {
      opacity: 0.6;
    }
  }

  &__toggle {
    display: none;
  }
}
