.catalog-card {
  &__img {
    margin-bottom: 57px;
  }

  &__heading {
    margin: 0;
    padding: 0;
    width: 336px;
    min-height: 54px;
    margin-bottom: 27px;
    font-size: 20px;
    font-weight: 600;
    line-height: normal;
    color: $color-night-rider;
  }

  &__text {
    margin: 0;
    padding: 0;
    margin-bottom: 25px;
    width: 336px;
    min-height: 108px;
    font-size: 16px;
    font-weight: 400;
    line-height: normal;
    color: $color-grey;
  }

  &__link {
    display: inline-block;
    margin-bottom: 31px;
    font-size: 16px;
    font-weight: 400;
    line-height: 14px;
    color: $color-denim;
    border: 1px solid $color-denim;
    border-radius: 5px;
    padding: 13px 21px;
  }
}
