.main-description {
  background-image: url('../img/bg/bg-inner-header.png');
  background-position: center;
  background-size: cover;
  margin-bottom: 99px;

  &__heading {
    margin: 0;
    padding: 0;
    margin-bottom: 250px;
    color: $color-default-white;
    font-size: 64px;
    font-weight: 700;
    line-height: 60px;
    text-transform: uppercase;
    display: none;
  }

  &__info {
    margin: 0;
    padding: 0;
    padding-top: 167px;
    padding-bottom: 323px;
    width: 760px;
    color: $color-default-white;
    font-size: 64px;
    font-weight: 700;
    line-height: 70px;
    text-transform: uppercase;
  }

  &__text {
    width: 850px;
    margin: 0;
    padding: 0;
    color: $color-default-white;
    font-size: 28px;
    font-weight: 400;
    line-height: 32px;
    display: none;
  }
}
